<template lang="html">
  <div class="page">
    <div id="map14"></div>
    <hr />
    <span class="back">
      <h2>{{addMap}}</h2>
      <h4>{{mess}}</h4>
      <b class="shixian">代码实现</b>
      <p class="diama">
        
      </p>
    </span>
    
  </div>
</template>

<script type="text/javascript">
  import $ from 'jquery';
  export default {

    data () {
        return {
          addMap:'绘制地图并给点绑定点击事件',
          mess:'这是直接根据api来绘制地图，并根据api来进行绘制地图上所需的点，自己来绑定点击事件处理。',
        }
    },
    mounted(){
      this.map();
    },
    methods:{
      map() {
        ma.drawMap({
           url: url,
           //url: "http://127.0.0.1",
           //source:new ol.source.OSM(),
           zoom:'5',
           name:'map14'
        });

        var urljson = "../../../../static/json/getStation.json";

        var st = new ol.style.Style({
            image: new ol.style.Icon({
                anchor: [0.5, 1],
                src: '../../../../static/image/point.png'
            })
        });
        var fun = function (){
          alert("绑定点击事件");
        }

        ma.clickPoint({
          url:urljson,
          st:st,
          fun:fun
        });
      }
    }
  };
</script>
<style lang="scss" scoped>
  
</style>



