<template lang="html">
  <div class="page">
    <div id="map18"></div>
    <hr />
    <span class="back">
      <h2>{{addMap}}</h2>
      <h4>{{mess}}</h4>
      <b class="shixian">代码实现</b>
      <p class="diama">
        
      </p>
    </span>
    
  </div>
</template>

<script type="text/javascript">
  import $ from 'jquery';
  export default {

    data () {
        return {
          addMap:'地图显示闪烁的点',
          mess:'这是直接根据api来绘制地图，并根据api来进行绘制点，并对点进行高亮显示',
        }
    },
    mounted(){
      this.map();
    },
    methods:{
      map() {
        ma.drawMap({
          url: url,
           //url: "http://127.0.0.1",
           //source:new ol.source.OSM(),
           zoom: '5',
           name: 'map18'
        });

        var st = new ol.style.Style({
            image: new ol.style.Circle({
                 radius: 8,
                 stroke: new ol.style.Stroke({
                     color: "gray"
                 }),
                 fill: new ol.style.Fill({
                     color: "yellow"
                 })
             })
            //image: new ol.style.Icon({
             //   anchor: [0.5, 1],
             //   src: '../../../static/image/point.png'
            //})
        });

        var source = new ol.source.Vector({
          loader: function() {
            $.get(
                '../../../static/json/getStation.json', //页面获取点
                {},
                function(data) {

                     var format = new ol.format.GeoJSON();
                     var feature = format.readFeatures(data, {
                      featureProjection: 'EPSG:3857'
                     });
                     source.addFeatures(feature);



                     // debugger
                     var le = data.features.length;
                     for(var a = 0;a<le;a++){
                        var parentdiv = $('<div></div>');
                        parentdiv.addClass('css_animation');
                        $('#map18').append(parentdiv);
                        for(var i=0;i<le;i++){
                          $('.css_animation').show();
                          ma.flickerPoint([data.features[i].properties.c_longitude,data.features[i].properties.c_latitude],le);
                        }
                     }
              },
            'json');
          }
        });

        var vector = new ol.layer.Vector({
            title:"title",
            source: source,
            style: st
        });
        map.addLayer(vector);
      },
      pointAnimate() {
        console.log(this.ar);
        //ma.pointAnimate(ar,styles);
      }
    }
  };
</script>
<style lang="scss" scoped> 

</style>



