<template lang="html">
  <div class="page">
    <div id="map16">
      <button class="btn btt btn-primary but">点击回放</button>
    </div>
    <hr />
    <span class="back">
      <h2>{{addMap}}</h2>
      <h4>{{mess}}</h4>
      <b class="shixian">代码实现</b>
      <p class="diama">
        
      </p>
    </span>
    
  </div>
</template>

<script type="text/javascript">
  import $ from 'jquery';
  export default {

    data () {
        return {
          addMap:'地图显示点的回放',
          mess:'这是直接根据api来绘制地图，并根据api来进行绘制点，并进行点的回放过程',
        }
    },
    mounted(){
      this.map();
    },
    methods:{
      map() {
        ma.drawMap({
        url: url,
           //url: "http://127.0.0.1",
           //source:new ol.source.OSM(),
           zoom:'5',
           name:'map16'
        });

        var styles = {
            'route': new ol.style.Style({
              stroke:new ol.style.Stroke({
                width:4,
                //color:[237,212,255,0.8]
                 color:"blue"
              })
            }),
            'icon':new ol.style.Style({
              image:new ol.style.Icon({
                anchor:[0.5,1],
                // src:'https://openlayers.org/en/v4.4.2/examples/data/icon.png'
                src: 'http://127.0.0.1/route_z/image/icon.png'
              })
            }),
            'geoMarker':new ol.style.Style({
              image:new ol.style.Circle({
                radius:5,
                snapToPixel:false,
                fill:new ol.style.Fill({
                  color:'black'
                }),
                stroke:new ol.style.Stroke({
                  color:'#fff',
                  width:2
                })
              })
            }),
        };

        $.get(
          '../../../../static/json/3.json', 
          {},
          function(data) {
              var format = new ol.format.GeoJSON();
              var feature = format.readFeatures(data, {
                  featureProjection: 'EPSG:3857'
              });
              var len = feature.length;
              var abr = [];
              for(var i= 0;i<len;i++){
                  var ar = feature[i].getGeometry().getCoordinates();
                  var aa = ol.proj.toLonLat(ar);
                  var a = aa[0];
                  var b = aa[1];
                  var arrr = [b,a];
                  abr.push(arrr);
              }
              // console.log(abr);
              var ar = [];
              function fun(abr){
                  for(var i=0;i<abr.length;i++){
                      if(Array.isArray(abr[i])){
                          fun(abr[i]);
                      }else{
                          ar.push(abr[i]);
                      }
                  }
              }
              fun(abr);
              $('.but').on('click',function(){
                ma.pointAnimate({
                  arr:ar,
                  style:styles
                });
              })
          }
        )
      },
      pointAnimate() {
        console.log(this.ar);
        //ma.pointAnimate(ar,styles);
      }
    }
  };
</script>
<style lang="scss" scoped>
  hr{
    margin-top:50px;
    border: 5px solid #ccc;
  }
</style>



