<template lang="html">
  <div class="page">
    <div id="map23">
      <button class="btn btn-primary btt" id="remove">清除</button>
    </div>
    <hr />
    <span class="back">
      <h2>{{addMap}}</h2>
      <h4>{{mess}}</h4>
      <b class="shixian">代码实现</b>
      <p class="diama">
        
      </p>
    </span>
    
  </div>
</template>

<script type="text/javascript">
  import $ from 'jquery';
  export default {

    data () {
        return {
          addMap:'地图上刪除绘制的点和线等',
          mess:'这是直接根据api来绘制地图，并根据api来将绘制好的覆盖物进行删除',
        }
    },
    mounted(){
      this.map();
    },
    methods:{
      map() {
        ma.drawMap({
          url: url,
           //url: "http://127.0.0.1",
           //source:new ol.source.OSM(),
           zoom: '5',
           name:'map23'
        });

        ma.drawPointOne({
            pointCoordinate: [115.1010,38.4908],
            radius: 8,
            Scolor: 'yellow',
            Fcolor: 'red',
        });

        ma.drawLineTwo({
          startCoordinate:[122.0004,40.8412],
          endCoordinate:[115.1010,38.4908],
        });

        $('#remove').on('click',function(){
          // console.log(12312);
          ma.clearLayer();
        })
        

      },
    }
  };
</script>
<style lang="scss" scoped> 
  hr{
    margin-top:50px;
    border: 5px solid #ccc;
  }
</style>



